
import { useList } from "@/hooks";
import { computed, defineComponent } from "@vue/runtime-core";
import { PullRefresh, List, Empty, Loading, Divider, Skeleton } from "vant";

function findParent(el: HTMLElement): HTMLElement | null {
  if (el.parentElement?.classList.contains("f-list-container")) {
    return el.parentElement;
  }
  if (el.parentElement) {
    return findParent(el.parentElement);
  }
  return null;
}

function setSkeleton(el: HTMLElement, binding: any) {
  const container = findParent(el);
  if (container) {
    container.style.height = binding.value ? "100%" : "";
  }
}

export default defineComponent({
  name: "FList",
  components: {
    "van-pull-refresh": PullRefresh,
    "van-list": List,
    "van-empty": Empty,
    "van-loading": Loading,
    "van-divider": Divider,
    "van-skeleton": Skeleton,
  },
  directives: {
    skeleton: {
      mounted: setSkeleton,
      beforeUnmount: (el) => {
        setSkeleton(el, { value: false });
      },
    },
  },
  props: {
    source: {
      type: Function,
    },
    pagination: {
      type: Boolean,
      default: true,
    },
    /**
     * 
     */
    first: {
      type: Boolean,
      default: false,
    },
    class: String,
    filter: {
      type: Function,
    },
  },
  setup(props) {
    console.debug("[Components] List Created");
    const {
      pageNo,
      items,
      pageSize,
      refreshing,
      onRefresh,
      onLoad,
      offset,
      finished,
      error,
      loading,
    } = useList<any>(props.source as any);
    if (!props.pagination && props.first && !loading.value) {
      onRefresh();
    }
    const listClass = `f-list ${props.class ? props.class : ""}`;

    const filterItems = computed<any[]>(() => {
      return props?.filter?.(items) || items;
    });

    const showEmpty = computed(() => {
      return (
        !loading.value &&
        (props?.filter ? filterItems.value.length === 0 : items.length === 0) &&
        !refreshing.value &&
        !error.value
      );
    });

    return {
      pageNo,
      items,
      filterItems,
      pageSize,
      refreshing,
      onRefresh,
      onLoad,
      offset,
      finished,
      error,
      loading,
      listClass,
      showEmpty,
    };
  },
});
